<template>
  <div class="content">
    <div style="margin-top:10px;">
        <div>
          <h4 class="title">ᠤᠲᠠᠴᠢ ᠰᠤᠷᠭᠠᠨ ᠬᠦᠮᠦᠵᠢᠯ —— ᠮᠤᠩᠭᠤᠯ ᠡᠮᠨᠡᠯᠭᠡ ᠶᠢᠨ ᠡᠮᠴᠢ ᠶᠢᠨ ᠬᠢᠷᠢ ᠭᠡᠮ ᠤᠯᠭᠤᠬᠤ ᠰᠢᠯᠭᠠᠯᠲᠠ ᠶᠢᠨ ᠰᠤᠷᠢᠯᠲᠠ ᠶᠢᠨ ᠰᠡᠳᠦᠪ</h4>
        </div>
        <div style="margin:0 20px;" onselectstart="return false">
          <div class="thumbnail">
              <p class="subject">
                {{qIndex+1}} . 
                <span>{{exams[qIndex].examTitle}}</span>
              </p>
              <ul style="padding-top:20px;">
                <li class="choices" v-for="(selection,index1) in exams[qIndex].selections" @click="checkEvent(index1)" :class="{active: activeIndex === index1, color1: index1 === rightIndex, color2: index1 === errorIndex}">
                  <span v-if="index1 === 0" :class="[{sign1: index1 === rightIndex}, {sign2: index1 === errorIndex}, 'sign']">A</span>
                  <span v-if="index1 === 1" :class="[{sign1: index1 === rightIndex}, {sign2: index1 === errorIndex}, 'sign']">B</span>
                  <span v-if="index1 === 2" :class="[{sign1: index1 === rightIndex}, {sign2: index1 === errorIndex}, 'sign']">C</span>
                  <span v-if="index1 === 3" :class="[{sign1: index1 === rightIndex}, {sign2: index1 === errorIndex}, 'sign']">D</span>
                  <span v-if="index1 === 4" :class="[{sign1: index1 === rightIndex}, {sign2: index1 === errorIndex}, 'sign']">E</span> {{selection}}
                </li>
              </ul>
          </div>
        </div>
    </div>

    <div class="btn-box">
      <div class="left">
        <div :class="[{button: qIndex !== 0}, {button1: qIndex === 0}]" @click="preEvent">ᠡᠮᠦᠨ᠎ᠡ ᠰᠡᠳᠦᠪ</div>
      </div>
      <div class="right">
        <div :class="[{button: qIndex !== exams.length - 1}, {button1: qIndex === exams.length - 1}]" @click="nextEvent">
          ᠳᠠᠷᠠᠭ᠎ᠠ ᠰᠡᠳᠦᠪ
        </div>
      </div>
    </div>
  </div>

</template>

<script>
  // import data from '../../static/77901.json'
  export default{
    name:"exam1",
    data(){
      return{
        exams:[],
        questionList: [],
        activeIndex: -1,
        rightIndex: -1,
        errorIndex: -1,
        ansState: false,
        allQuestionState: [],
        errorHtmlData: [],
        collectionList:[],
        collectionState: false,
        qIndex:0
      }
    },
    watch:{
      qIndex(n, o){
        this.qIndex = n
        this.qIndexChange()
      }
    },
    mounted(){
      for (var i = 0; i < this.exams.length; i++) {
        this.collectionList.push(false);
        this.allQuestionState.push({id: i, state: 1});// state 1:未做   2:对    3:错
      }
    },
    created(){
      this.fnLuanXu()
    },
    methods:{
      checkEvent (num) {
        console.log(num)
        if (!this.questionList[this.qIndex]) { // 判断当前题目是否做过
          console.log("num")
          this.activeIndex = num;
          if (this.exams[this.qIndex].answer === num) { // 做对了
            console.log("做对了")
            this.rightIndex = num;
            this.questionList[this.qIndex] = {userAns: num, rightAns: num};
            this.allQuestionState[this.qIndex].state = 2;
          } else { // 做错了
            console.log("做错了")
            this.errorIndex = num;
            this.rightIndex = this.exams[this.qIndex].answer;
            this.questionList[this.qIndex] = {userAns: num, rightAns: this.exams[this.qIndex].answer};
            this.errorHtmlData.push(this.qIndex + 1);
            this.allQuestionState[this.qIndex].state = 3;
          }
        }
      },
      preEvent () {
        if (this.qIndex !== 0) {
          this.qIndex -= 1;
          this.activeIndex = -1;
          this.rightIndex = -1;
          this.errorIndex = -1;
          this.ansState = false;
        }
      },
      nextEvent () {
        if (this.exams.length > this.qIndex + 1) {
          this.qIndex += 1;
          this.activeIndex = -1;
          this.rightIndex = -1;
          this.errorIndex = -1;
          this.ansState = false;
        }
      },
      qIndexChange () {
        this.activeIndex = -1;
        this.rightIndex = -1;
        this.errorIndex = -1;
        if (this.questionList[this.qIndex]) { // 已答列表是否存在
          if (this.questionList[this.qIndex].rightAns === this.questionList[this.qIndex].userAns) { // 答案是否正确
            this.rightIndex = this.questionList[this.qIndex].rightAns;
          } else {
            this.rightIndex = this.questionList[this.qIndex].rightAns;
            this.errorIndex = this.questionList[this.qIndex].userAns;
          }
        }
        if (this.collectionList[this.qIndex]) { // 判断是否收藏
          this.collectionState = true;
          this.collectionText = '取消收藏';
        } else {
          this.collectionState = false;
          this.collectionText = '收藏';
        }
      },
      fnLuanXu() {
        this.$http.get('./../static/77901.json')
        .then(function(data){
          console.log(data.json());
          return data.json();
        })
          .then(function(data){
            // this.exams = data;
            var aLuanXu = data.list;
            var num = aLuanXu.length;
            console.log("num::::::::::"+num);
            for (var i = 0; i < num; i++) {
                var iRand = parseInt(num * Math.random());
                var temp = aLuanXu[i];
                aLuanXu[i] = aLuanXu[iRand];
                aLuanXu[iRand] = temp;
            }
            console.log(aLuanXu);
            this.exams = aLuanXu;
            })
      }
    }
  }
</script>

<style scoped>
  .content{
    box-sizing: border-box;
    margin: 10px auto;
  }
  .panel-heading{
    border: none;
  }
  .title{
    color: var(--theme-color);
    padding-top: 0px;
    margin-left: 20px;
    font-family: urga;
    line-height: 1;
    text-align: left;
    font-size: 10px;
  }
  .subject{
    line-height: 2;
    padding: 10px;
    margin-right: 0;
    font-size: 16px;
    color: #333;
  }
  .choices{
    list-style-type: none;
    line-height: 18px;
    cursor: pointer;
    background-color: #f5f5f5;
    margin-right: 5px;
    padding: 5px;
  }
  .thumbnail{
    margin:0 10px;
    border-radius: 0;
  }
  .panel{
    border: none;
  }
  .row{
    background-color: var(--background-color);
  }
  .btn-diy{
    position: relative;
    float: right;
    background-color: #333;
    color: white;
    border: 1px solid #fff;
    border-radius: 25px;
    width: 50px;
    height: 50px;
    padding-left: 10px;
    bottom: 20px;
    left: 20px;
  }
  .button {
    width: 80%;
    line-height: 40px;
    background: #00a400;
    display: block;
    text-align: center;
    color: #fff;
    border-radius: 5px;
    cursor: pointer;
  }
  .button:active{
      background: green;

  }
  .button1 {
    cursor: pointer;
    width: 80%;
    line-height: 40px;
    display: block;
    text-align: center;
    border-radius: 5px;
    background: #ddd;
    color: #333;
  }
  .button1:active{
      background: green;
  }
  .sign {
    width: 20px;
    height: 20px;
    line-height: 18px;
    border-radius: 50%;
    display: inline-block;
    text-indent: 5.5px;
    border: 1px solid #555;
    color: #333;
    font-size: 12px;
  }
  .sign1 { /*成功*/
    color: #fff;
    background: #00a400;
    border: 1px solid #00a400;
  }
  .sign2 { /*失败*/
    color: #fff;
    background: #e8571b;
    border: 1px solid #e8571b;
  }
  .color1 {
    color: #00a400;
  }
  .color2 {
    color: #e8571b;
  }
  .btn-box{
    /*background-color: transparent;*/
    flex:1;
    margin-top: 10px;
    height: 320px;
    float: right;
  }
  .left{
    height: 150px;
    margin-right: 10px;
    /* display: inline; */
    float: left;
  }
  .right{
    height: 150px;
    /* display: inline; */
    float: right;
  }

</style>


