<template>
  <div id="app">
    <Exam/>
  </div>
</template>

<script>
import Exam from './components/Exam'

export default {
  name: 'App',
  components: {
    Exam
  }
}
</script>

<style>
    :root{
      --theme-color:#375e96;
      --footer-color:#333333;
      --background-color: #f0f3f9;
    }
    @font-face {
      font-family: 'iconfont';  /* project id 544540 */
      src: url('//at.alicdn.com/t/font_544540_b58m6utb6b6gvi.eot');
      src: url('//at.alicdn.com/t/font_544540_b58m6utb6b6gvi.eot?#iefix') format('embedded-opentype'),
      url('//at.alicdn.com/t/font_544540_b58m6utb6b6gvi.woff') format('woff'),
      url('//at.alicdn.com/t/font_544540_b58m6utb6b6gvi.ttf') format('truetype'),
      url('//at.alicdn.com/t/font_544540_b58m6utb6b6gvi.svg#iconfont') format('svg');
    }
    html,body{
    	margin:0;
    	padding:0;
    	width:100%;
    	height: 100%;
    	overflow:scroll;  
	    overflow-y:hidden;  
	    background: var(--background-color);
    }
#app {
  margin:0;
  /*padding-right等于footer的高度*/
  padding:0;
  -moz-writing-mode: vertical-lr;
  -webkit-writing-mode: vertical-lr;
  -o-writing-mode: vertical-lr;
  -ms-writing-mode: tb-lr;
  writing-mode: vertical-lr;
  -webkit-text-orientation: sideways-right;
  line-height: 1.5;
  page-break-before: auto;
  page-break-after: always;
  min-height: 100%;
  height: auto !important;
  height:100%;
  min-width: 100%;
  position: relative;
}
</style>



